.clipper {
  position: relative;
  display: inline-block;
  color: #333333;
  text-decoration: none; }
  .clipper:focus {
    outline: none; }
  .clipper__overlay {
    background-color: #333333;
    color: #e9e9e7;
    left: 0;
    top: 0;
    position: absolute;
    -webkit-transition: -webkit-clip-path ease-in-out 200ms;
    transition: -webkit-clip-path ease-in-out 200ms;
    -o-transition: clip-path ease-in-out 200ms;
    transition: clip-path ease-in-out 200ms;
    transition: clip-path ease-in-out 200ms, -webkit-clip-path ease-in-out 200ms; }



